.profile {
    background-color: rgb(234, 237, 248);
    padding: 60px;
    width: 500px;
    margin-right: auto;
    margin-left: auto;
}
.description {
    background-color: #fff;
    width: 300px;
    height: 300px;
    padding: 10px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    color: #454545;
}
.avatar {
    width: 78px;
    min-height: 78px;
    border-radius: 50%;
    overflow: hidden;
    background-color: yellow;
}
.name {
    margin-top: 30px;
    margin-bottom: 10px;

    font-style: bold;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.19;
    text-align: center;
}
.tag {
    margin-top: 0;
    margin-bottom: 16px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.19;
    text-align: center;
}
.location {
    margin-top: 0;
    margin-bottom: 16px;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.19;
    text-align: center;
}
.stats {
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    height: 60px;
    padding: 0;
    list-style: none;
    justify-content: center;
    align-items: center;
    margin-right: auto;
    margin-left: auto;
    margin-top: 0;
    background-color: rgb(214, 203, 221);
    border-radius: 8px;
}
.statsContainer {
    display: block;
width: 100px;

}
.label {
display: flex;
justify-content: center;
font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.19;
    text-align: center;

}
.quantity {
display: flex;
justify-content: center;
font-style: bold;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.19;
    text-align: center;
}